@import url(https://fonts.googleapis.com/css?family=Roboto|Sriracha&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.flex-container {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-wrap: wrap;
          flex-wrap: wrap;
  -webkit-justify-content: center;
          justify-content: center;
  background-color: rgb(36, 36, 36);
}

.flex-container > div {
  -webkit-flex-direction: row;
          flex-direction: row;
  background-color: #f1f1f1;
  width: 100px;
  margin: 10px;
  text-align: center;
  line-height: 25px;
  font-size: 15px;
  flex-direction: row;
}

/* Variables */
:root {
  --margin-left: 15vw;
}

/* Logo, cím, 2 gomb */
.header {
  background-image: url(/komposztfront/static/media/intro.6ed3d31d.png);
  background-size: 100%;
  height: 120vw;
  background-repeat: no-repeat;
}

/* Header Mobil */
@media (max-width: 767px) {
  .header {
    background-size: 640px auto;
    height: 120vw;
  }
}

/* Ez szünteti meg a fehér sávot a képernyő tetején. Ne töröld*/
header {
  height: 0;
}

/* Menü fonts */
/* To do: 40px szavak közt */
.navbar-light .navbar-nav .nav-link {
  color: #ffffff !important;
  font-weight: 700 !important;
  padding-left: 20px !important;
  padding-right: 20px !important;
}

.navbar-light .navbar-nav .nav-link:hover {
  color: #ce653d !important;
}

.nav-link-active {
  color: #ce653d !important;
  text-decoration: underline !important;
}

.navbar {
  background: transparent;
  font-size: 18 !important;
  font-family: "Quicksand", sans-serif;
}

.navbar.active {
  background: #4e4e4e;
  opacity: 0.9;
}

.logo {
  width: 13vw;
  margin-left: 15vw;
  margin-left: var(--margin-left);
  padding-top: 8vw;
}

.title {
  text-align: start;
  color: aliceblue;
  font-size: 3vw;
  text-transform: uppercase;
  font-family: "Montserrat", sans-serif;
  font-weight: 800;
  text-transform: uppercase;
  letter-spacing: 0.1em;
  margin-left: 15vw;
  margin-left: var(--margin-left);
  margin-top: 5vh;
  margin-bottom: 2vh;
}

/* @media (max-width: 767px) {
  .title {
    font-size: 30px;
    margin-left: 0px;
  }
} */

.button1:hover {
  color: #ce653d !important;
  font-family: "Quicksand", sans-serif;
}

.button {
  background-color: #ce653d;
  font-weight: 500;
  border: none;
  color: white;
  padding: 15px 32px;
  text-align: center;
  text-decoration: none;
  display: inline-block;
  font-size: 2vw;
  width: 45vh;
  margin: 2vh;
  margin-left: 15vw;
  margin-left: var(--margin-left);
  border-radius: 2.25em;
  font-family: "Montserrat", sans-serif;
  text-transform: uppercase;
  font-weight: bold;
}

.reg {
  background-color: white;
  color: #ce653d;
}

.button:hover {
  color: #ce653d;
  background-color: white;
}

.reg:hover {
  color: white;
  background-color: #ce653d;
}

@media (max-width: 767px) {
  .button1 {
    margin: auto;
    margin-left: auto;
    margin-right: auto;
    font-size: 18px;
  }
}

.fittop {
  margin-top: 0px !important;
  padding-top: 0px !important;
}

.divheight {
  min-height: 80vh;

  line-height: 80vh;
}

.divheight p {
  line-height: 1.5;
  display: inline-block;
  vertical-align: middle;
}

.gyik {
  min-height: 80vh;
  padding-left: 20vh;
  padding-right: 20vh;
  line-height: 1.5;
}

h1 {
  font-family: "Montserrat", sans-serif;
  text-transform: uppercase;
  margin-top: 0;
  color: #ce653d;
  font-weight: bold;
}

h2 {
  color: white;
  font-family: "Montserrat", sans-serif;
  text-transform: uppercase;
}

p {
  font-family: "Quicksand", sans-serif;
  vertical-align: middle;
  max-width: 1000px;
  margin-left: auto;
  margin-right: auto;
}

.socialicon {
  text-align: right;
  font-size: 40px;
  margin-left: 15px;
  margin-right: 15px;
}

.row-title {
  font-family: "Montserrat", sans-serif;
  text-transform: uppercase;
  font-weight: bold;
}

.row-content-text {
  font-family: "Quicksand", sans-serif;
}

