#login-container {
  height: 100px;
  width: 150px;
  padding: 5px;
  border-radius: 5px;
  background: white;
  position: relative;
  box-shadow: 5px 5px 10px rgba(0, 0, 0, 0.5);
}
.profile-img {
  height: 40px;
  width: 40px;
  background: url("https://pngimage.net/wp-content/uploads/2018/06/icon-png-home-9.png");
  background-size: cover;
  background-position: center;
  position: absolute;
  top: -25px;
  left: -25px;
  border-radius: 50%;
  box-shadow: 5px 5px 10px rgba(0, 0, 0, 0.5);
}

.profile-img-store {
  height: 40px;
  width: 40px;
  background: url("https://d1nhio0ox7pgb.cloudfront.net/_img/o_collection_png/green_dark_grey/512x512/plain/store.png");
  background-size: cover;
  background-position: center;
  position: absolute;
  top: -25px;
  left: -25px;
  border-radius: 50%;
  box-shadow: 5px 5px 10px rgba(0, 0, 0, 0.5);
}

.nonevisible {
  display: none;
}

.description {
  margin-bottom: 5px;
}
.social {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: space-between;
          justify-content: space-between;
  -webkit-align-items: center;
          align-items: center;
  width: calc(100% - 40px);
  margin: 0 auto;
}
.link {
  text-align: center;
  border: solid 2px green;
  width: 75px;
  padding: 5px 0;
  border-radius: 5px;
}
