.cardbutton {
  width: 80%;
  height: 80px;
  font-size: 2rem;
  margin: 30px 10% 0 10%;
  color: wheat;
  border: none;
  box-shadow: 5px 5px 10px rgba(0, 0, 0, 0.5);
  border-radius: 5px;
  background: linear-gradient(45deg, wheat, wheat, wheat, wheat);
  background-size: 300% 300%;
  outline: none;
  transition: all 200ms ease-in-out;
}
